import React, { useEffect, useState } from 'react'
// import { useSelector } from 'react-redux'
import Aos from "aos"
import "aos/dist/aos.css"

import Header from './Header'


const Home = () => {
    useEffect(() => {
        Aos.init({duration: 1500, once: true});                    

    }, []);


    return (
        <div className="home-container">
            <Header  />
        </div>
    )
}

export default Home
